body {
  font-family: "Short Stack", cursive;
  color: #111111;
  padding: 10px;
  background-image: url(/hangman-game/static/media/grid2.5abc703f.png); }

img.home {
  position: absolute;
  height: 60px;
  width: 60px;
  padding: 5px;
  margin: 5px;
  cursor: pointer;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform;
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform; }

img.home:hover, .home:focus, .home:active {
  -webkit-transform: translateX(-8px);
  transform: translateX(-8px); }

ul.alphabet {
  font-family: "Short Stack", cursive;
  font-size: 50px;
  margin-left: 120px;
  text-transform: uppercase; }

.letter {
  display: inline-block;
  width: 45px;
  height: 45px;
  margin: 3px;
  padding: 20px;
  line-height: 1;
  text-align: center;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  box-sizing: content-box;
  -webkit-transition: all 0.1s ease;
  transition: all 0.1s ease; }

.disabled {
  display: inline-block;
  width: 45px;
  height: 45px;
  margin: 3px;
  padding: 20px;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: center;
  pointer-events: none;
  box-sizing: content-box;
  -webkit-transition: all 0.1s ease;
  transition: all 0.1s ease; }

.titleContent {
  width: 100%;
  margin-left: -10px;
  float: left;
  overflow: hidden; }

.gameTitle {
  font-size: 50px;
  text-align: center; }

.lives {
  position: relative;
  font-size: 35px;
  font-family: "Short Stack", cursive;
  float: right;
  z-index: 10000; }

.wordContainer {
  height: 100px;
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  margin-left: 70px;
  margin-bottom: 50px;
  float: right; }

ul.wordToGuess {
  font-size: 50px;
  letter-spacing: 30px;
  margin-left: 300px;
  font-family: "Short Stack", cursive;
  text-transform: uppercase;
  text-align: center;
