#winBackground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.6;
  filter: alpha(opacity=60);
  z-index: 5; }

#winContainer {
  position: absolute;
  height: 360px;
  width: 560px;
  padding: 20px;
  text-align: center;
  margin-top: 13%;
  margin-left: 30%;
  border: 3px solid #111111;
  border-radius: 4px;
  background-image: url(/hangman-game/static/media/grid2.5abc703f.png);
  z-index: 100;
  -webkit-animation: scale-up-center 0.4s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  animation: scale-up-center 0.4s cubic-bezier(0.39, 0.575, 0.565, 1) both; }

#winText {
  font-size: 35px;
  color: #111111;
  margin-top: 50px;
